
import XHeader from 'vux/src/components/x-header/index.vue'

import IconMenu from '@/components/IconMenu/iconmenu.vue'
import Tabs from '@/components/tabs/tabs';
export default {
data() {
return {
tab:1,
lists:[],
isloading: false,
panes:[{
label:'专家门诊',
code:1,
},
{
label:'内科',
code:2,
},
{
label:'外科',
code:3,
},
{
label:'妇产科',
code:4,
},
{
label:'儿科',
code:5,
},
{
label:'五官科',
code:6,
},
{
label:'其他',
code:7,
},
{
label:'皮肤行病科',
code:8,
}],
dataSeeIng:[],
list:{},
CBM: '',
headerObj:{},
leftList:[],
rightList:[],
leftOn: 0,
cacheObj:{}
}
},
beforeRouteLeave(to, from, next){  // 用于做缓存

if (to.name == 'doctorList') {
localStorage.setItem('hospitalCBM', this.CBM)
}else{
localStorage.removeItem('hospitalCBM')
}
next();
},
mounted(){
this.CBM = window.localStorage.hospitalCBM || this.$route.params.details.CBM;   
this.init();  
this.getLeftList();
},
methods: {
changeTab(n){
if (n != this.tab) {
this.tab = n;
}
},
init(){  //  初始化医院详情
this.$ajax.post(this.$root.host,{
"MSH.1": "hospitalregistration",
"MSH.2": "getHospital",
cbm: this.CBM,  // 机构编码
token:this.$root.loginInfo.token,

}).then(res => {
console.log(res);
this.headerObj = res;
this.CBM = this.headerObj.CBM;
}).catch(error => {
console.log(error);
})
},
getLeftList(){  // 加载一级科室
this.$ajax.post(this.$root.host,{
"MSH.1": "hospitalregistration",
"MSH.2": "listHospitalKS",
cbm: this.CBM,  // 机构编码
sjksbm: '',  // 科室编码,如果是一级科室 传空
token:this.$root.loginInfo.token,

}).then(res => {
if(typeof res.rows != 'undefined' && res.rows.length > 0){
this.leftList = this.leftList.concat(res.rows);
this.getRightList(this.leftList[0].CKSBM,0);
}
}).catch(error => {
console.log(error);
})
},
getRightList(str,index){  // 加载二级科室
this.$ajax.post(this.$root.host,{
"MSH.1": "hospitalregistration",
"MSH.2": "listHospitalKS",
cbm: this.CBM,  // 机构编码
sjksbm: str,   // 科室编码
token:this.$root.loginInfo.token,

}).then(res => {
if(typeof res.rows != 'undefined' && res.rows.length > 0){
this.rightList = this.rightList.concat(res.rows);
this.cacheObj['index'+index] = this.rightList;
}
}).catch(error => {
console.log(error);
})
},
queryKS(obj,index){

this.leftOn = index;
this.rightList = [];
if(this.cacheObj['index'+index]){
this.rightList = this.cacheObj['index'+index];
return;
}
this.getRightList(obj.CKSBM,index);
},

getDJ(str){
switch (str) {
case '1':
return '特等'
break;
case '2':
return '甲等'
break;
case '3':
return '乙等'
break;
case '4':
return '合格'
break;
case '5':
return '未定'
break;

default:
return str;
break;
}
},

},
components:{
XHeader,Tabs
}
}



